  margin-bottom: 1.5rem;
}

.accordion-item {
  padding: 0.75rem 0;
}
.accordion-item > [data-toggle=collapse] {
  transition: margin 0.35s ease, opacity 0.35s ease;
  cursor: pointer;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
